export const rowStyle = (row, rowIndex) => {
  const style = {};
  if (rowIndex%2===0) {
  	style.backgroundColor = '#8F979E'
  } else {
	style.backgroundColor = 'white';
  }
  style.color = 'black';
  style.fontWeight = 'bold';
  return style;
};


export const defaultSorted = [{
  dataField: 'AVG', 
  order: 'asc' 
}];

const headStyle = {
	backgroundColor: 'grey',
	fontWeight: 800,
	width: '10%'
}

export const colPlayers = [
{
  dataField: 'key',
  text: 'Rank',
  sort: true,
  headerStyle: headStyle
},
{
  dataField: 'name',
  text: 'Player',
  sort: true,
  headerStyle: headStyle
}, {
  dataField: 'team',
  text: 'Team',
  sort: true,
  headerStyle: headStyle
}, {
  dataField: 'bye',
  text: 'Bye',
  sort: true,
  headerStyle: headStyle
}, {
  dataField: 'YAH',
  text: 'Yahoo',
  sort: true,
  sortFunc: (a,b,order,dataField) => {if (a==='-'){a = 999;} if (b==='-'){b = 999;}if (order === 'asc') {return b - a;}return a - b;},
  headerStyle: headStyle
}, {
  dataField: 'CBS',
  text: 'CBS',
  sort: true,
  sortFunc: (a,b,order,dataField) => {if (a==='-'){a = 999;} if (b==='-'){b = 999;}if (order === 'asc') {return b - a;}return a - b;},
  headerStyle: headStyle
}, {
  dataField: 'ESP',
  text: 'ESPN',
  sort: true,
  sortFunc: (a,b,order,dataField) => {if (a==='-'){a = 999;} if (b==='-'){b = 999;}if (order === 'asc') {return b - a;}return a - b;},
  headerStyle: headStyle
},{
  dataField: 'FOX',
  text: 'Fox',
  sort: true,
  sortFunc: (a,b,order,dataField) => {if (a==='-'){a = 999;} if (b==='-'){b = 999;}if (order === 'asc') {return b - a;}return a - b;},
  headerStyle: headStyle
}, {
  dataField: 'NFL',
  text: 'NFL',
  sort: true,
  sortFunc: (a,b,order,dataField) => {if (a==='-'){a = 999;} if (b==='-'){b = 999;}if (order === 'asc') {return b - a;}return a - b;},
  headerStyle: headStyle
}, {
  dataField: 'AVG',
  text: 'AVG',
  sort: true,
  headerStyle: headStyle
},{
  dataField: 'DIF',
  text: 'DIF',
  sort: true,
  sortFunc: (a,b,order,dataField) => {
  	if (a==='-'){
  		a = -999;
  	} 
  	if (b==='-'){
  		b = -999;
  	}
  	if (order === 'desc') {
  		if(a===-999){
  			a = 999;
  		}
  		if(b===-999){
  			b = 999;
  		}
  		return a - b;
  	}
  	return b - a;
  },
  headerStyle: headStyle
}, {
  dataField: 'STD',
  text: 'STD',
  sort: true,
  headerStyle: headStyle
}
];

